"use client";

import * as React from "react";
import { Star, ExternalLink, Plus, Trash2 } from "lucide-react";

interface Review {
    id: string;
    guestName: string;
    country: string;
    rating: number;
    text: string;
    source: string;
    date: string;
}

const initialReviews: Review[] = [
    { id: "1", guestName: "Elizabeth", country: "United Kingdom", rating: 10, text: "Very welcoming host. Well equipped. Great location. Quiet", source: "Booking.com", date: "Jan 2026" },
    { id: "2", guestName: "Inge", country: "Belgium", rating: 10, text: "Wonderful hospitable owners! Beautiful rooms with a lovely terrace. Mosta is a charming town.", source: "Booking.com", date: "Jan 2026" },
    { id: "3", guestName: "Julie", country: "United Kingdom", rating: 10, text: "Location very central and easy to catch buses. The rooftop is perfect for relaxing.", source: "Booking.com", date: "Dec 2025" },
    { id: "4", guestName: "Shane", country: "Canada", rating: 10, text: "The hosts were amazing. Great restaurant recommendations with authentic Maltese cuisine.", source: "Booking.com", date: "Dec 2025" },
    { id: "5", guestName: "Tamara", country: "Slovakia", rating: 10, text: "We loved everything! The room is spacious and the terrace is outstanding.", source: "Booking.com", date: "Nov 2025" },
    { id: "6", guestName: "Marco", country: "Germany", rating: 10, text: "Perfect stay in Malta! The hosts made us feel at home. Great views from the terrace.", source: "Booking.com", date: "Nov 2025" },
];

export default function AdminReviewsPage() {
    const [reviews, setReviews] = React.useState<Review[]>(initialReviews);
    const [showAddForm, setShowAddForm] = React.useState(false);
    const [newReview, setNewReview] = React.useState<Partial<Review>>({
        rating: 10,
        source: "Booking.com",
    });

    const handleAdd = () => {
        if (newReview.guestName && newReview.text) {
            setReviews([
                {
                    id: Date.now().toString(),
                    guestName: newReview.guestName,
                    country: newReview.country || "Unknown",
                    rating: newReview.rating || 10,
                    text: newReview.text,
                    source: newReview.source || "Direct",
                    date: new Date().toLocaleDateString("en-US", { month: "short", year: "numeric" }),
                },
                ...reviews,
            ]);
            setNewReview({ rating: 10, source: "Booking.com" });
            setShowAddForm(false);
        }
    };

    const handleDelete = (id: string) => {
        if (confirm("Remove this review from display?")) {
            setReviews(reviews.filter(r => r.id !== id));
        }
    };

    return (
        <>
            <header className="bg-white border-b border-stone-200 px-6 py-4 flex items-center justify-between">
                <div>
                    <h1 className="text-xl font-semibold text-stone-800">Reviews</h1>
                    <p className="text-stone-500 text-sm">Manage guest testimonials displayed on site</p>
                </div>
                <div className="flex items-center gap-3">
                    <a
                        href="https://www.booking.com/hotel/mt/villa-mosta.html"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="flex items-center gap-2 border border-stone-300 text-stone-700 px-4 py-2 rounded-lg hover:bg-stone-50 transition-colors"
                    >
                        <ExternalLink className="w-4 h-4" />
                        <span>Booking.com</span>
                    </a>
                    <button
                        onClick={() => setShowAddForm(true)}
                        className="flex items-center gap-2 bg-amber-600 text-white px-4 py-2 rounded-lg hover:bg-amber-700 transition-colors"
                    >
                        <Plus className="w-4 h-4" />
                        <span>Add Review</span>
                    </button>
                </div>
            </header>

            <div className="p-6">
                {/* Add Review Form */}
                {showAddForm && (
                    <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
                        <h3 className="font-semibold text-stone-800 mb-4">Add New Review</h3>
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                            <input
                                type="text"
                                placeholder="Guest Name"
                                value={newReview.guestName || ""}
                                onChange={(e) => setNewReview({ ...newReview, guestName: e.target.value })}
                                className="border border-stone-300 rounded-lg px-3 py-2"
                            />
                            <input
                                type="text"
                                placeholder="Country"
                                value={newReview.country || ""}
                                onChange={(e) => setNewReview({ ...newReview, country: e.target.value })}
                                className="border border-stone-300 rounded-lg px-3 py-2"
                            />
                        </div>
                        <textarea
                            placeholder="Review text..."
                            value={newReview.text || ""}
                            onChange={(e) => setNewReview({ ...newReview, text: e.target.value })}
                            className="w-full border border-stone-300 rounded-lg px-3 py-2 mb-4"
                            rows={3}
                        />
                        <div className="flex items-center gap-4">
                            <select
                                value={newReview.rating}
                                onChange={(e) => setNewReview({ ...newReview, rating: Number(e.target.value) })}
                                className="border border-stone-300 rounded-lg px-3 py-2"
                            >
                                {[10, 9, 8, 7, 6].map(n => (
                                    <option key={n} value={n}>{n}/10</option>
                                ))}
                            </select>
                            <select
                                value={newReview.source}
                                onChange={(e) => setNewReview({ ...newReview, source: e.target.value })}
                                className="border border-stone-300 rounded-lg px-3 py-2"
                            >
                                <option value="Booking.com">Booking.com</option>
                                <option value="Airbnb">Airbnb</option>
                                <option value="Google">Google</option>
                                <option value="Direct">Direct</option>
                            </select>
                            <div className="flex-1" />
                            <button
                                onClick={() => setShowAddForm(false)}
                                className="px-4 py-2 text-stone-600 hover:bg-stone-100 rounded-lg"
                            >
                                Cancel
                            </button>
                            <button
                                onClick={handleAdd}
                                className="px-4 py-2 bg-amber-600 text-white rounded-lg hover:bg-amber-700"
                            >
                                Add Review
                            </button>
                        </div>
                    </div>
                )}

                {/* Reviews List */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    {reviews.map((review) => (
                        <div key={review.id} className="bg-white rounded-lg shadow-sm p-6">
                            <div className="flex items-start justify-between mb-3">
                                <div>
                                    <p className="font-medium text-stone-800">{review.guestName}</p>
                                    <p className="text-stone-500 text-sm">{review.country} â€¢ {review.date}</p>
                                </div>
                                <div className="flex items-center gap-2">
                                    <div className="flex items-center gap-1 bg-stone-100 px-2 py-1 rounded">
                                        <Star className="w-4 h-4 text-amber-500 fill-current" />
                                        <span className="font-semibold text-stone-800">{review.rating}</span>
                                    </div>
                                    <button
                                        onClick={() => handleDelete(review.id)}
                                        className="p-1 text-stone-400 hover:text-red-500"
                                    >
                                        <Trash2 className="w-4 h-4" />
                                    </button>
                                </div>
                            </div>
                            <p className="text-stone-600 text-sm mb-2">&ldquo;{review.text}&rdquo;</p>
                            <p className="text-stone-400 text-xs">via {review.source}</p>
                        </div>
                    ))}
                </div>
            </div>
        </>
    );
}
